<template>
    
    <div>
        <button type="button" :class="props.buttonClass" @click="dialogVisible = true" v-html="props.buttonText"></button>

        <Dialog v-model:visible="dialogVisible" modal header="Yeni Ek Ücret Ekle" :style="{ width: '50vw' }" :breakpoints="{ '960px': '75vw', '641px': '95vw' }">
            <form @submit.prevent="create">

                <div class="row g-5 mb-5">
                    <div class="col-lg-12">
                        <div class="d-flex align-items-center mb-1">
                            <span class="fw-semibold">İsim</span>
                            <div class="d-flex align-items-center ms-2">
                                <img :src="`/dmn/media/flags/${props.language}.svg`" class="me-2" style="width: 14px" />
                                <span class="text-uppercase">{{ props.language }}</span>
                            </div>
                        </div>
                        <InputText v-model="form.name" class="w-100" />
                    </div>
                    <div class="col-lg-12">
                        <label class="form-label">Ücret</label>
                        <InputText v-model="form.price" class="w-100" />
                    </div>
                    <div class="col-lg-12">
                        <label class="form-label">Kısa Açıklama</label>
                        <Textarea autoResize rows="1" cols="30" class="w-100" v-model="form.description" />
                    </div>
                    <div class="col-lg-12">
                        <label class="form-label">Kullanılacak Ürün Tipi</label>
                        <Dropdown v-model="form.product_type_id" :options="props.productTypes" optionGroupLabel="name" optionLabel="name" optionValue="id" optionGroupChildren="product_types" placeholder="Lütfen seçiniz" :maxSelectedLabels="5" class="w-100" />
                    </div>
                </div>

                <div class="separator my-10"></div>
                <button type="submit" class="btn btn-primary" :class="{ 'opacity-25': form.processing }" :disabled="form.processing">Kaydet</button>
                <FormErrors :form="form"/>

            </form>
        </Dialog>
    </div>

</template>

<script setup>

import {ref} from "vue";
import { useForm } from "@inertiajs/vue3";
import Dialog from "primevue/dialog";
import InputText from 'primevue/inputtext';
import Textarea from "primevue/textarea";
import Dropdown from "primevue/dropdown";

const props = defineProps({
    productTypes:Object,
    language:{
        type:String,
        default:'tr'
    },
    fromCost:{
        type:Object,
        default:null
    },
    buttonClass:{
        type:String,
        default:'btn fw-bold btn-sm btn-success d-flex align-items-center'
    },
    buttonText:{
        type:String,
        default:'<i class="bi bi-person-plus fs-4"></i><span class="ms-1 lh-1">YENİ</span>'
    },
});

const dialogVisible = ref(false);

const form = useForm({
    name:props.fromCost ? props.fromCost.name+' - '+props.language : "",
    language:props.language,
    description:props.fromCost ? props.fromCost.description : "",
    price:props.fromCost ? props.fromCost.price : null,
    product_type_id:"",
    uuid:props.fromCost ? props.fromCost.uuid : ""
});

const create = () => {
    form.post(route('extra-costs.store'),{
        onSuccess: () => {
            form.reset();
            dialogVisible.value = false;
        },
    });
}

</script>